<template lang="html">
  <div class="header">
    <h1>Formular za ocenjivanje kompetencija - Konacna Procena</h1>
  </div>
  <div class="formular">
    <table id="formular">
      <tr>
        <th>Kompetence :</th>
        <th>Ponder</th>
        <th>Samoprocena</th>
        <th>Konacna Procena</th>
        <th>Rangiranje</th>
      </tr>
      <tr>
        <td>Inicijativa</td>
        <td>15%</td>
        <td>Iznad očekivanja</td>
        <td>
          <select name="type" id="type" v-model="inicijativa" @change="onChangeInicijativa($event)">
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
        <td>{{inicijativaRang}}</td>
      </tr>
      <tr>
        <td>Orijentisanost ka Klijentima</td>
        <td>15%</td>
        <td>Iznad očekivanja</td>
        <td>
          <select name="type" id="type" v-model="orijentisanostKaKlijentima" @change="onChangeOrijentisanost($event)">
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
        <td>{{orijentisanostRang}}</td>
      </tr>
      <tr>
        <td>Timski rad</td>
        <td>12.5%</td>
        <td>Iznad očekivanja</td>
        <td>
          <select name="type" id="type" v-model="timskiRad"  @change="onChangeTimskiRad($event)">
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
        <td>{{timskiRadRang}}</td>
      </tr>
      <tr>
        <td>Lični razvoj i razvoj drugih</td>
        <td>12.5%</td>
        <td>Iznad očekivanja</td>
        <td>
          <select name="type" id="type" v-model="licniRazvoj" @change="onChangeLicniRazvoj($event)">
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
        <td>{{licniRazvojRang}}</td>
      </tr>
      <tr>
        <td>Odnos prema radu, radnim zadacima i sredstvima rada</td>
        <td>15%</td>
        <td>Iznad očekivanja</td>
        <td>
          <select name="type" id="type" v-model="odnos" @change="onChangeOdnos($event)">
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
        <td>{{odnosRang}}</td>
      </tr>
      <tr>
        <td>Kvalitet</td>
        <td>15%</td>
        <td>Iznad očekivanja</td>
        <td>
          <select name="type" id="type" v-model="kvalitet" @change="onChangeKvalitet($event)">
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
        <td>{{kvalitetRang}}</td>
      </tr>
      <tr>
        <td>Odgovornost</td>
        <td>15%</td>
        <td>Iznad očekivanja</td>
        <td>
          <select name="type" id="type" v-model="odgovornost" @change="onChangeOdgovornost($event)">
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
        <td>{{odgovornostRang}}</td>
      </tr>
      <tr>
        <td colspan="4"></td>

        <td>{{ukupno.toFixed(2)}}</td>
      </tr>
    </table>
  </div>

  <div class="rezultat1" v-if="ukupno<0.75">
    <p>Rezultati loši, potrebno unapređenje za vršenje trenutne uloge ili prelazak na drugo radno mesto	</p>
  </div>
  <div class="rezultat2" v-if="ukupno>=0.75 && ukupno< 0.99">
    <p>Rezultati dobri, ali  neophodan razvoj za vršenje trenutne uloge
</p>
  </div>
  <div class="rezultat3" v-if="ukupno>=0.99 && ukupno< 1.17">
    <p>Apsolutno ostvarenje rezultata u trenutnoj ulozi
</p>

  </div>
  <div class="rezultat4" v-if="ukupno>=1.17">
    <p>Prebačaj rezultata u trenutnoj ulozi
</p>

  </div>
  <div class="komentar">
    <h4>Komentar</h4>
    <input
      class="form-control"
      type="text"
      placeholder=""
      name="komentar"
      v-model="komentar"
      id="komentar"
      >
  </div>
</template>

<script>
import {mapGetters} from 'vuex';
export default {
  computed: mapGetters(['user']),
  data(){
    return{
      odgovori:['Ne Zadovoljava Ocekivanja','Potrebno Poboljsanje','Zadovoljava Ocekivanja','Iznad Ocekivanja'],
      komentar:"",
      inicijativaRang:0,
      orijentisanostRang:0,
      timskiRadRang:0,
      licniRazvojRang:0,
      odnosRang:0,
      kvalitetRang:0,
      odgovornostRang:0,
      ukupno:0,
    }
  },
  methods:{
    onChangeInicijativa(event){
      if(event.target.value==='Ne Zadovoljava Ocekivanja') this.inicijativaRang=0.08;
      if(event.target.value==='Potrebno Poboljsanje') this.inicijativaRang=0.11;
      if(event.target.value==='Zadovoljava Ocekivanja') this.inicijativaRang=0.15;
      if(event.target.value==='Iznad Ocekivanja') this.inicijativaRang=0.19;
      this.ukupno=this.inicijativaRang+this.orijentisanostRang+this.timskiRadRang+this.licniRazvojRang+this.odnosRang+this.kvalitetRang+this.odgovornostRang;
    },
    onChangeOrijentisanost(event){
      if(event.target.value==='Ne Zadovoljava Ocekivanja') this.orijentisanostRang=0.08;
      if(event.target.value==='Potrebno Poboljsanje') this.orijentisanostRang=0.11;
      if(event.target.value==='Zadovoljava Ocekivanja') this.orijentisanostRang=0.15;
      if(event.target.value==='Iznad Ocekivanja') this.orijentisanostRang=0.19;
      this.ukupno=this.inicijativaRang+this.orijentisanostRang+this.timskiRadRang+this.licniRazvojRang+this.odnosRang+this.kvalitetRang+this.odgovornostRang;
    },
    onChangeTimskiRad(event){
      if(event.target.value==='Ne Zadovoljava Ocekivanja') this.timskiRadRang=0.06;
      if(event.target.value==='Potrebno Poboljsanje') this.timskiRadRang=0.09;
      if(event.target.value==='Zadovoljava Ocekivanja') this.timskiRadRang=0.13;
      if(event.target.value==='Iznad Ocekivanja') this.timskiRadRang=0.16;
      this.ukupno=this.inicijativaRang+this.orijentisanostRang+this.timskiRadRang+this.licniRazvojRang+this.odnosRang+this.kvalitetRang+this.odgovornostRang;
    },
    onChangeLicniRazvoj(event){
      if(event.target.value==='Ne Zadovoljava Ocekivanja') this.licniRazvojRang=0.06;
      if(event.target.value==='Potrebno Poboljsanje') this.licniRazvojRang=0.09;
      if(event.target.value==='Zadovoljava Ocekivanja') this.licniRazvojRang=0.13;
      if(event.target.value==='Iznad Ocekivanja') this.licniRazvojRang=0.16;
      this.ukupno=this.inicijativaRang+this.orijentisanostRang+this.timskiRadRang+this.licniRazvojRang+this.odnosRang+this.kvalitetRang+this.odgovornostRang;
    },
    onChangeOdnos(event){
      if(event.target.value==='Ne Zadovoljava Ocekivanja') this.odnosRang=0.08;
      if(event.target.value==='Potrebno Poboljsanje') this.odnosRang=0.11;
      if(event.target.value==='Zadovoljava Ocekivanja') this.odnosRang=0.15;
      if(event.target.value==='Iznad Ocekivanja') this.odnosRang=0.19;
      this.ukupno=this.inicijativaRang+this.orijentisanostRang+this.timskiRadRang+this.licniRazvojRang+this.odnosRang+this.kvalitetRang+this.odgovornostRang;
    },
    onChangeKvalitet(event){
      if(event.target.value==='Ne Zadovoljava Ocekivanja') this.kvalitetRang=0.08;
      if(event.target.value==='Potrebno Poboljsanje') this.kvalitetRang=0.11;
      if(event.target.value==='Zadovoljava Ocekivanja') this.kvalitetRang=0.15;
      if(event.target.value==='Iznad Ocekivanja') this.kvalitetRang=0.19;
      this.ukupno=this.inicijativaRang+this.orijentisanostRang+this.timskiRadRang+this.licniRazvojRang+this.odnosRang+this.kvalitetRang+this.odgovornostRang;
    },
    onChangeOdgovornost(event){
      if(event.target.value==='Ne Zadovoljava Ocekivanja') this.odgovornostRang=0.08;
      if(event.target.value==='Potrebno Poboljsanje') this.odgovornostRang=0.11;
      if(event.target.value==='Zadovoljava Ocekivanja') this.odgovornostRang=0.15;
      if(event.target.value==='Iznad Ocekivanja') this.odgovornostRang=0.19;
      this.ukupno=this.inicijativaRang+this.orijentisanostRang+this.timskiRadRang+this.licniRazvojRang+this.odnosRang+this.kvalitetRang+this.odgovornostRang;
    }
  }
}
</script>

<style lang="css" scoped>
.rezultat1{
  background-color: red;
  color: white;
  border-style: solid;
  border-color: black;
  border-width: medium;
  border-radius: 15px;
  padding:10px;
  text-align: center;
  border-top: 0px;
}
.rezultat2{
  background-color: yellow;
  color: white;
  border-style: solid;
  border-color: black;
  border-width: medium;
  border-radius: 15px;
  padding:10px;
  text-align: center;
  border-top: 0px;
}
.rezultat3{
  background-color: cyan;
  color: white;
  border-style: solid;
  border-color: black;
  border-width: medium;
  border-radius: 15px;
  padding:10px;
  text-align: center;
  border-top: 0px;
}
.rezultat4{
  background-color: green;
  color: white;
  border-style: solid;
  border-color: black;
  border-width: medium;
  border-radius: 15px;
  padding:10px;
  text-align: center;
  border-top: 0px;
}
p{
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
table, th, td{
  border: 1px solid black;
  text-align: center;
}
tr:nth-child(even){background-color: #bbbbbb;}
tr:nth-child(odd){background-color: #ffffff;}
tr:hover {background-color: #afa;}
table{
   margin: auto;
}
.header{
  background-color: #ffaaaa;
  padding: 15px;
  border-style: solid;
  border-color: black;
  border-width: medium;
  border-radius: 15px;
}
.formular{
  background-color: #ffffaa;
  border-style: solid;
